.post{
    width: 385px;
    margin: 0px 122px 40px 30px;
}
.postImg{
    width: 120%;
    height: 300px;
    object-fit: cover;
    border-radius: 7px;
}
.postInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.postCat{
    font-family: 'Varela Round', sans-serif;
    font-size: 13px;
    color: #be9656;
    line-height: 20px;
    margin-right: 10px;
    margin-top: 15px;
    cursor: pointer;
}
.postTitle{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 20px;
    font-weight: 600;
    margin-top: 15px;
    cursor: pointer;
}
.postDate{
    font-family: 'Lora', serif;
    font-style: italic;
    font-size: 13px;
    color: rgb(46, 43, 43);
    margin-top: 15px;
}
.postDesc{
    font-family: 'Varela Round', sans-serif;
    font-size: 14px;
    counter-reset: #444;
    line-height: 24px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}